@import url(https://fonts.googleapis.com/css2?family=Dosis&display=swap);
body {
  background-color: #05053c;
  background-image: linear-gradient(
    0deg,
    #05053c 0%,
    rgba(255, 255, 255, 1) 72%,
    rgba(255, 255, 255, 1) 100%
  );
  background-repeat: no-repeat;
  color: #fff;
  height: 100vh;
  margin: 0;
  font-family: "Dosis", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
}

.search__input {
  border: dashed 1px blue;
  border-radius: 25px;
  font-family: "Dosis", sans-serif;
  font-size: 1.2rem;
  text-align: center;
  margin-right: 2%;
  stroke-dasharray: 19;
  width: 20%;
}

.search__input:focus {
  -webkit-animation: 1.5s inputsearch forwards;
          animation: 1.5s inputsearch forwards;
  outline: none;
}

.search__button {
  background-color: #c90028;
  font-family: "Dosis", sans-serif;
  font-size: 1.2rem;
  font-weight: 600;
  height: 40px;
  width: 40px;
  -webkit-justify-content: center;
          justify-content: center;
  border: 0;
  border-radius: 50%;
  color: #fff;
}

.search__button:focus {
  -webkit-animation: 1.5s searching;
          animation: 1.5s searching;
}

@keyframes inputsearch {
  from {
    border: dashed 1px blue;
  }
  to {
    border: dashed 1px red;
  }
}

@-webkit-keyframes inputsearch {
  from {
    border: dashed 1px blue;
  }
  to {
    border: dashed 1px red;
  }
}

@keyframes searching {
  0% {
    background-color: #c90028;
  }
  50% {
    background-color: #05053c;
  }
  100% {
    background-color: #c90028;
  }
}

@-webkit-keyframes searching {
  0% {
    background-color: #c90028;
  }
  50% {
    background-color: #05053c;
  }
  100% {
    background-color: #c90028;
  }
}

@media only screen and (max-width: 750px) {
  .search__input {
    width: 80%;
  }
}

.searchResults {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.images {
  padding: 2%;
  list-style: none;
  -webkit-flex: 0 0 28%;
          flex: 0 0 28%;
  -webkit-animation: fadein 3.6s;
          animation: fadein 3.6s;
}

.images:nth-of-type(1) {
  -webkit-animation: fadein 0.6s;
          animation: fadein 0.6s;
}

.images:nth-of-type(2) {
  -webkit-animation: fadein 1.2s;
          animation: fadein 1.2s;
}

.images:nth-of-type(3) {
  -webkit-animation: fadein 1.8s;
          animation: fadein 1.8s;
}

.images:nth-of-type(4) {
  -webkit-animation: fadein 2.4s;
          animation: fadein 2.4s;
}

.images:nth-of-type(5) {
  -webkit-animation: fadein 3s;
          animation: fadein 3s;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.images img {
  width: 100%;
  border-radius: 25px;
}

@media only screen and (max-width: 750px) {
  .images {
    -webkit-flex: 0 0 45%;
            flex: 0 0 45%;
  }
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}

.nasa_image {
  margin: auto;
  width: 20%;
}

